:host {
  /**
   * @prop --c-tab-item-padding: Tab item padding
   */
  --_c-tab-item-padding: var(--c-tab-item-padding, 16px 0 0 0);

  display: block;
  min-width: 100%;
  padding: var(--_c-tab-item-padding);
  height: 0;
  user-select: none;
}

:host([active="true"]) {
  height: auto;
  user-select: auto;
}

:host([active="false"]) > div {
  display: none;
}

:host([disabled="true"]) {
  min-width: 0;
  width: 0;
  overflow: hidden;
}
